import React from 'react'
import Keyletters from './Keyletters'

export default function Keyborad(props) {
  function getLetter(v){
    props.sendLetter(v)
  }
    const qwerty=['q','w','e','r','t','y','u','i'
  ,'o','p','a','s','d','f','g','h','j','k','l',
  'z','Enter','x','c','v','b','n','m','Delete']
    const letters=[]
    function returnLetter(i){
      return(
        <Keyletters
        key={"Keyletter"+i}
         letter={qwerty[i]}
         toggle={(v)=>getLetter(v)}
        />
      )
    }
    for(let i=0;i<28;i++){
        letters[i]=returnLetter(i);
    }
  return (
    <div className="Keyboard">{letters}</div>
  )
}
